@import "../mixins.scss";

.hero {
    min-height: 100vh;
    padding: 100px 0;
    position: relative;

    &__container {
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-width: 0;
    }

    &__figure-one {
        position: absolute;
        top: -70px;
        left: 0;
        z-index: 1;
    }

    &__figure-two {
        position: absolute;
        top: -70px;
        right: 0;
        z-index: 1;
    }

    &__figure-three {
        position: absolute;
        bottom: -24%;
        left: 36%;
        z-index: 1;

        @include mobile {
            display: none;
        }
    }


    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        z-index: 10;

        @include desktop-sm {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding-top: 60px;
        }
    }

    &__subtitle {
        font-size: 14px;
        font-weight: 700;
        color: var(--color-accent);
        margin-bottom: 15px;
    }

    &__title {
        margin: 0;
        font-size: 64px;
        font-weight: 700;
        line-height: 64px;
        color: var(--color-accent);
        margin-bottom: 20px;

        @include mobile {
            font-size: 31px;
            line-height: 34px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        color: #666666;
        margin-bottom: 40px;
        max-width: 570px;
    }

    &__img {
        position: relative;
        flex: 0 0 50%;
    }

    &__main-img {
        @include desktop-sm {
            width: 100%;
            object-fit: cover;
        }
       
    }

    &__price {
        position: absolute;
        bottom: 40px;
        right: 23px;
        z-index: 10;

        &-text {
            display: block;
            font-size: 14px;
            font-weight: 700;
            color: var(--color-dark);
            margin-bottom: 11px;
        }

        &-num {
            display: block;
            font-size: 46px;
            line-height: 48px;
            font-weight: 700;
            color: #05297F;
        }

        @include tablet-lg {
            bottom: -14px;
        }
    }
}


.hero__slider {
    width: 100%;

    &-button{
     position: relative;
     margin-top: 50px;
     height: 50px;
    }
}

.swiper-button-next,
.swiper-button-prev {
    width: 40px;
    height: 40px;
    background-color: transparent;
    border: none;
}
.swiper-button-prev {
    background-image: url("/img/prev.svg");
}

.swiper-button-next {
    top: 25px;
    left: 123px;
    background-image: url("/img/next.svg");
}

.swiper-button-next:after, .swiper-button-prev:after {
    /* font-family: swiper-icons; */
    font-size: 0;
    /* text-transform: none !important; */
    /* letter-spacing: 0; */
    /* text-transform: none; */
    /* font-variant: initial; */
    /* line-height: 1;*/
}

.swiper-button-next.swiper-button-disabled, 
.swiper-button-prev.swiper-button-disabled {
    opacity: .35;
    cursor: auto;
    pointer-events: none;
}
