@import "../mixins.scss";

.header {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    padding-top: 20px;

    &__container {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;

        @include desktop-sm {
            align-items: center;
        }
    }

    &__buttons {
        display: flex;
        align-items: center;

        li {

            margin-left: 20px;

            &:first-child {
                margin-left: 0;
            }

            @include desktop-sm {
                margin-left: 0px;

                &:nth-child(1) {
                    display: none;
                }

                &:nth-child(3) {
                    display: none;
                }
            }

           
        }

    }
}


.nav {

    &__list {
        display: flex;

        @include tablet-lg {
            flex-direction: column;
            padding: 34px 20px 20px 20px;
            max-width: 340px;
            height: 100%;
            background-color: #fff;
        }
    }

    &__item {
        & + & {
            margin-left: 70px;
        }

        &-mobile {
            display: none;
        }
        
        @media(max-width: 1200px) {
            & + & {
                margin-left: 30px;
            }
        }

        @include tablet-lg {
            margin-bottom: 20px;
            
            & + & {
                margin-left: 0px;
            }

            &-mobile {
                display: block;
                margin-bottom: 20px;
            }

        }

        
    }

    &__link {
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        color: var(--color-dark);
        text-transform: uppercase;
        transition: color .3s ease;
        outline: none;

        &:hover {
            color: var(--color-accent);
        }

        &.focus-visible {
            color: var(--color-accent);
        }
    }

    @include tablet-lg {
        display: block;
        left: -100%;
        position: absolute;
        top: 0;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100vh;
        transition: all .5s;

        &.header__nav--active {
            left: 0;
        }
    }

}



.burger {
    --burger-width: 24px;
    --burger-height: 20px;
    --burger-line-height: 2px;

    @include burger;
    display: none;
    color: var(--color-dark);
    z-index: 100;
    
    @media(max-width: 1025px) {
        display: block;
        margin-left: 15px;
    }


    &.burger--active {
        color: var(--color-white);
    }
}

.scroll {
	position: relative;
	overflow: hidden;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
}