.footer {
    padding: 164px 0;
    background-color: #EEF2F5;

    @include tablet-ld {
        padding: 84px 0;
    }

    @media(max-width:480px) {
        padding: 60px 0;
    }

    &__nav {
        margin-bottom: 60px;
        
        @media(max-width:480px) {
            overflow-x: scroll;
        }

        &-list {
            justify-content: center;

            @media(max-width:480px) {
                justify-content: flex-start;
            }
        }

        &-item {
            border-right: 4px solid #E5E5E5;
            padding: 0 20px;

            &:last-child {
                border-right: 0;
            }

            @media(max-width:480px) {
                padding: 0 14px;
            }
        }
    }

    &__wrapper {
        display: grid;
        grid-template-columns: repeat(12,1fr);

        @media(max-width:990px) {
            grid-template-columns: repeat(6,1fr);
        }

        @media(max-width:480px) {
            grid-template-columns: repeat(3,1fr);
        }
    }

    &__item {
        grid-column: 3 span;

        ul {

            li {
                margin-bottom: 5px;

                a {
                    font-weight: 300;
                    font-size: 18px;
                    line-height: 24px;
                    color: #666666;
                    transition: color .3s ease;

                    &:hover {
                        color: #05297F;
                    }
                }
            }
        }

        @media(max-width:990px) {
            margin-bottom: 30px;
        }
    }

    &__phone {
        display: flex;
        align-items: center;
        margin-bottom: 17px;
        div {
            margin-left: 10px;
            span {
                display: block;
                font-weight: 300;
                font-size: 14px;
                text-transform: uppercase;
                color: var(--color-dark);
            }

            a {
                font-weight: 600;
                font-size: 24px;
                color: var(--color-dark);
            }
        }
    }

    &__work-time {
        display: block;
        font-weight: 300;
        font-size: 18px;
        line-height: 24px;
        color: #666666;
        margin-bottom: 30px;
    }

    &__socials {
        display: flex;

        &-item {
            & + & {
                margin-left: 30px;
            }
        }
    }

    &__section-name {
        font-weight: 500;
        font-size: 22px;
        color: var(--color-dark);
        margin-bottom: 20px;
    }

    &__payment-list {
        display: flex;
    }

    &__payment-item {

        & + & {
            margin-left: 20px;
            margin-bottom: 0;
        }
    }
}
