@import "../mixins.scss";

.collection {
    padding: 100px 0;
    position: relative;

    &__bg {
        position: absolute;
        bottom: -343px;
        left: 0;
        z-index: 1;
    }

    @include mobile {
        padding: 80px 0;
    }

    &__container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr auto 33%;
        grid-column-gap: 150px;
        grid-template-areas: "collection__item-girl collection__item-singup"
                              "collection__item-girl collection__item-man" 
                              "collection__item-sneaker collection__item-sneaker";
        grid-row-gap: 120px;
        position: relative;
        z-index: 100;
        
        @include desktop-sm {
            grid-template-areas: "collection__item-girl collection__item-singup"
            "collection__item-man collection__item-man" 
            "collection__item-sneaker collection__item-sneaker";
            grid-column-gap: 70px;
            grid-row-gap: 70px;
        }

        @include tablet-ld {
            grid-template-areas: "collection__item-girl collection__item-girl"
            "collection__item-singup collection__item-singup"
            "collection__item-man collection__item-man" 
            "collection__item-sneaker collection__item-sneaker";
            grid-column-gap: 40px;
            grid-row-gap: 40px;
            grid-template-rows: 1fr auto 1fr 1fr;
        }
    }

    &__item {

        position: relative;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &-girl {
            grid-area: collection__item-girl;
            max-height: 600px;
            position: relative;

            &::after {
                content: "Girl Collection";
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                font-weight: 700;
                font-size: 14px;
                line-height: 18px;
                text-transform: uppercase;
                color: var(--color-dark);
                transform: rotate(-90deg);
            }

            @include mobile {
                &::after {
                    display: none;
                }
            }

        }

        &-man {
            grid-area: collection__item-man;
            position: relative;

            &::after {
                content: "Boys Collection";
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                font-weight: 700;
                font-size: 14px;
                line-height: 18px;
                text-transform: uppercase;
                color: var(--color-dark);
                transform: rotate(-90deg);
            }

            @include desktop-sm {
                grid-column: 2 span;

                &::after {
                    top: 20px;
                }
            }

            @include mobile {
                &::after {
                    display: none;
                }
            }
        }

        &-sneaker {
            max-width: 950px;
            grid-area: collection__item-sneaker;
            position: relative;


            &::after {
                content: "Sale";
                position: absolute;
                top: 28px;
                right: -55px;
                width: 114px;
                height: 114px;
                background-color: #FC9D9D;
                border-radius: 50%;
                font-weight: bold;
                font-size: 28px;
                line-height: 34px;
                color:var(--color-white);
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                text-transform: uppercase;
            }

            @include desktop-sm {
                max-width: 100%;
                grid-column: 2 span;

                &::after {
                    top: 0px;
                    right: 0px;
                }
            }

            @include mobile {
                &::after {
                    width: 75px;
                    height: 75px;
                    font-size: 20px;
                }
            }

        }

        &-singup {
            grid-area: collection__item-singup;
            padding-top: 100px;
            
            @include tablet-ld {
                grid-column: 2 span;
                padding-top: 0px;
            }
        }
    }

    &__about {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 1;
        padding: 0 0 50px 50px;

        &-link {
            display: block;
            font-weight: 700;
            font-size: 28px;
            line-height: 34px;
            color: var(--color-dark);
            margin-bottom: 23px;
        }

        @include mobile {
            padding: 0 0 20px 20px;
        }
    }


    &__title {
        font-size: 46px;
        line-height: 56px;
        font-weight: 700;
        color: #05297F;
        margin-bottom: 30px;

        span {
            color: #252525;
        }

        @include tablet-ld {
            font-size: 40px;
            line-height: 50px;
            margin-bottom: 15px;
        }

        @media(max-width:414px) {
            font-size: 30px;
            line-height: 34px;
        }
    }

    &__text {
        font-family: "Roboto";
        font-weight: 300;
        font-size: 16px;
        line-height: 24px;
        color: #666666;
        margin-bottom: 50px;

        @include tablet-ld {
            margin-bottom: 25px;
        }
    }


}