@import "../mixins.scss";

.products {
    padding: 100px 0;
    position: relative;

    @include mobile {
        padding: 80px 0;
    }

    &__bg {
        position: absolute;
        top: -112px;
        right: 0;
        z-index: 1;
    }

    &__container {
        position: relative;
        z-index: 100;
    }

    &__title {
        text-align: center;
        font-size: 54px;
        line-height: 60px;
        font-weight: 700;
        text-transform: uppercase;
        color: #252525;
        margin-bottom: 66px;

        @include tablet-ld {
            font-size: 41px;
        }

        @include mobile {
            font-size: 31px;
            margin-bottom: 36px;
        }

        @include mobile-sm {
            font-size: 28px;
            margin-bottom: 20px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-column-gap: 30px;
        margin-bottom: 70px;
        
        &-item {
            grid-column: 3 span;
        }

        @include desktop-sm {
            grid-template-columns: repeat(6, 1fr);

            &-item {
              margin-bottom: 30px;
            }
        }

        @media(max-width:640px) {
            display: flex;
             grid-column-gap: 0px;
             margin-bottom: 70px;

            &-item {
                margin-bottom: 0px;
            }
        }
    }

    &-slider {
        position: relative;
    }

}


.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    &__img {
        width: 100%;
        position: relative;
        margin-bottom: 16px;

        img {
            display: block;
            width: 100%;
            object-fit: cover;
        }
    }

    &__btns {
        position: absolute;
        bottom: 0;
        right: 20px;
        z-index: 1;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

        li {
            margin-bottom: 10px;
        }
    }

    &:hover &__btns {
        opacity: 1;
        visibility: visible;
    }

    &__sale::after {
        content: "-20%";
        position: absolute;
        top: 20px;
        right: 23px;
        z-index: 1;
        font-weight: 300;
        font-size: 12px;
        line-height: 12px;
        color: var(--color-white);
        width: 41px;
        height: 21px;
        border-radius: 50px;
        background-color: #D63447;
        padding: 2px 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

    &:hover.product__sale::after {
        opacity: 1;
        visibility: visible;
    }

    &__rating {
        margin-bottom: 20px;
    }

    &__name-wrap {
        margin-bottom: 20px;

        span {
            display: block;
            text-align: center;
            font-weight: 300;
            font-size: 14px;
            line-height: 14px;
            text-transform: uppercase;
            color: #B1B1B1;
            margin-bottom: 5px;
        }

        a {
            font-size: 20px;
            line-height: 28px;
            color: #252525;
        }
    }

    &__price-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__price {
        font-weight: 600;
        font-size: 22px;
        color: #D63447;
        margin-left: 10px;
        position: relative;

        &-sale::before {
            content: "$175";
            position: absolute;
            top: 4px;
            right: -34px;
            font-size: 16px;
            line-height: 16px;
            text-decoration-line: line-through;
            color: #B1B1B1;
        }
    }
}


.swiper-container-horizontal > .swiper-pagination-bullets, 
.swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: -40px;
}

.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    display: inline-block;
    border-radius: 50%;
    background: #000;
    opacity: .2;
}

.swiper-pagination-bullet-active {
    opacity: 1;
    background: var(--color-accent);
}